import React, { Component } from 'react'
import { BrowserRouter as Router, Route } from 'react-router-dom'
import MuiThemeProvider from 'material-ui/styles/MuiThemeProvider'
import darkBaseTheme from 'material-ui/styles/baseThemes/darkBaseTheme'
import getMuiTheme from 'material-ui/styles/getMuiTheme'

import MenuPf from './components/Menu'
import Contact from './components/Contact'
import Projects from './components/Projects'
import About from './components/About'

import './App.css'

let g = 'color:#efdefd;font-weight:bold;'
let b = 'color:#3BA7B6;font-weight:bold;'
let message = `
%cXX    XX          XX   XX         %cXXXXXX XXXXXX    
%cXX    XX          XX   XX         %cX      X
%cXX    XX          XX   XX         %cX   XX X   XX
%cXXXXXXXX XXXXXXXX XX   XX XXXXXXX %cX    X X    X
%cXX    XX XX    XX XX   XX XX   XX %cXXXXXX XXXXXX
%cXX    XX XXXXXXXX XX   XX XX   XX %c
%cXX    XX XX       XX   XX XX   XX %c
%cXX    XX XXXXXXXX XX   XX XXXXXXX %c
`
class App extends Component {
  constructor() {
    super()

    this.state = {
      projLoading: true,
      infoLoading: true,
      information: [],
      projects: []
    }
  }

  componentDidMount() {
    fetch('https://gg-portfolio-api.herokuapp.com/information')
      .then(data => data.json())
      .then(data =>
        this.setState({
          infoLoading: false,
          information: data[0]
        })
      )
    fetch('https://gg-portfolio-api.herokuapp.com/projects')
      .then(data => data.json())
      .then(data =>
        this.setState({
          projLoading: false,
          projects: data
        })
      )
    console.log(message, g, b, g, b, g, b, g, b, g, b, g, b, g, b, g, b)
  }

  render() {
    return (
      <Router>
        <MuiThemeProvider muiTheme={getMuiTheme(darkBaseTheme)}>
          <main>
            <MenuPf />
            <Route
              exact
              path="/"
              render={() => {
                return <About state={this.state} />
              }}
            />
            <Route
              path="/projects"
              render={() => {
                return <Projects state={this.state} />
              }}
            />
            <Route
              path="/contact"
              render={() => {
                return <Contact state={this.state} />
              }}
            />
          </main>
        </MuiThemeProvider>
      </Router>
    )
  }
}

export default App



