import React from 'react'
import CircularProgress from 'material-ui/CircularProgress'
import './style/text.css'

const Contact = ({ state }) => {
  return (
    <div className="widthLimit">
      {state.infoLoading ? (
        <div>
          <h3>Waking up MongoDB</h3>
          <CircularProgress size={250} thickness={5} />
          <div>
            <h3>Free !== Fast</h3>
          </div>
        </div>
      ) : (
        <div className="contact">
          <span>
            <h2>Email:</h2>
            <p>
              <a href={`mailto:${state.information.email}`} target="_blank">
                {state.information.email}
              </a>
            </p>
          </span>
          <span>
            <h2>Twitter:</h2>
            <p>
              <a href={'https://twitter.com/GrantGlidewell'} target="_blank">
                {state.information.phone}
              </a>
            </p>
          </span>
          <span>
            <h2>Medium:</h2>
            <p>
              <a href={'https://medium.com/@grant.glidewell'} target="_blank">
                @Grant.Glidewell
              </a>
            </p>
          </span>
          <span>
            <h2>Github:</h2>
            <p>
              <a href={state.information.github} target="_blank">
                {state.information.github}
              </a>
            </p>
          </span>
        </div>
      )}
    </div>
  )
}

export default Contact



